/* forum-regular - cyrillic */
@font-face {
    font-family: 'Forum';
    font-style: normal;
    font-weight: 400;
    src: local('Forum Regular'),
         url('../fonts/forum/forum-v11-cyrillic-regular.woff2') format('woff2'), /* Super Modern Browsers */
         url('../fonts/forum/forum-v11-cyrillic-regular.woff') format('woff'); /* Modern Browsers */
    font-display: swap;
  }