.main-nav {
    position: relative;
}

.main-nav--closed {
    .main-nav__list {
        display: none;
    }
}

.main-nav--nojs {
    .main-nav__toggle {
        display: none;
    }
}

.main-nav__toggle {
    position: absolute;
    &::before {
        position: absolute;
        content: "";
        display: block;
        background-color: $white;
        height: 1px;
    }

    &::after {
        position: absolute;
        content: "";
        display: block;
        background-color: $white;
        height: 1px;
    }
}

.main-nav__toggle-text {
    display: none;
    padding-left: 38px;
    font-family: $font;
    color: $white;
    font-size: 20px;
    line-height: 22px;
    text-transform: uppercase;
}

@media (min-width: $min-desktop-width) {
        .main-nav__toggle--opened {
            bottom: 847px;
            right: 0;
            width: 34px;
            height: 34px;
            // border: 2px solid red;
            &::before {
                top: 15px;
                left: -3px;
                width: 40px;
                transform: rotate(45deg);
            }

            &::after {
                top: 15px;
                left: -5px;
                width: 40px;
                transform: rotate(-45deg);
            }
        }

        .main-nav__toggle--closed {
            bottom: 338px;
            right: 0;
            width: 102px;
            height: 40px;
            padding: 0;

            &::before {
                top: 15px;
                left: -3px;
                width: 25px;
            }

            &::after {
                top: 21px;
                left: -3px;
                width: 25px;
            }

            .main-nav__toggle-text {
                display: block;
            }
        }
}